import React, { Fragment, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { getTrademarksList } from "../../api/trademarkProducts";
import { numbericalAlphabeticalSort } from "../../utils/array";
import BannerSlider from "../home/components/banners/bannerSlider";

function TradeMarks() {
	let keys = [];
	let numAlSort = [];
	const [trademarks, setTradeMarks] = useState();
	const { t } = useTranslation();
	const banners = useSelector((state) => state.any?.banners);

	if (trademarks) {
		numAlSort = numbericalAlphabeticalSort(trademarks.items);
		keys = Object.keys(numAlSort);
	}

	const scroll = window.addEventListener("scroll", () => {
		const div = document.querySelector(".trademarks-page");
		const header = document.querySelector(".trademarks-header");

		if (div && header) {
			if (window.scrollY > 140) {
				div.classList.add("fixed");
				header.classList.add("fixed");
			} else {
				div.classList.remove("fixed");
				header.classList.remove("fixed");
			}
		}
	});
	const bannerCenter = banners.filter(
		(banner) => banner.position === "center"
	);

	useEffect(() => {
		return () => {
			window.removeEventListener("scroll", scroll);
		};
	}, []);

	useEffect(() => {
		const catElements = document.querySelectorAll(".idElements");
		const anchors = document.querySelectorAll(".anchor");
		anchors.forEach((a) => {
			a.addEventListener("click", (e) => {
				e.preventDefault();
				catElements.forEach((el) => {
					if (a.href.includes(el.id)) {
						window.scrollTo(
							0,
							el.offsetTop +
								Math.abs(el.getBoundingClientRect().top) -
								Math.abs(el.getBoundingClientRect().top) +
								window.innerWidth <
								991
								? 70
								: 170
						);
						el.classList.add("active");
					} else el.classList.remove("active");
				});
			});
		});
	}, [window.location.hash]);

	useEffect(() => {
		getTrademarksList().then((res) => {
			setTradeMarks(res);
		});
	}, []);

	return (
		<div className="trademarks-page">
			<div className="container">
				<h3 className="title">Популярные бренды</h3>
				<BannerSlider banner={bannerCenter} position={"center"} />
			</div>
			<div className="row trademarks-header">
				<div className="col-12">
					<div className="container ">
						<div className="header-content">
							<h3>{t("all-trademarks")}</h3>
							{/* <div className="brend-search">Search</div> */}
							{keys.map((item, index) => (
								<a
									key={index}
									href={`/trademarks#${item}`}
									className="col-lg-3 col-md-4 anchor col-6"
								>
									{item}
								</a>
							))}
						</div>
					</div>
				</div>
			</div>

			<div className="col-12 menus">
				<div className="container">
					<div className="row">
						{keys.map((item, i) => (
							<Fragment key={i}>
								<h3
									className="category-title idElements col-12"
									id={`${item}`}
								>
									{item}
								</h3>

								{numAlSort.hasOwnProperty(`${item}`)
									? numAlSort[item].map((link, index) => (
											<div
												className="col-6 col-lg-3 col-md-4 pl-0"
												key={index}
											>
												<Link
													to={`/trademark/${link}`}
													key={index}
													className="nav-link"
												>
													{link}
												</Link>
											</div>
									  ))
									: null}
							</Fragment>
						))}
					</div>
				</div>
			</div>
		</div>
	);
}

export default TradeMarks;
